export default {
  layout: 'home',
  async fetch() {
    this.planets = await fetch('https://api.nuxtjs.dev/planets').then((res) =>
      res.json()
    )
  },
  // eslint-disable-next-line vue/order-in-components
  data() {
    return {
      planets: [],
    }
  },
}
